import axios from 'axios';

const API = 'http://127.0.0.1:3333/aJatzsXzLO2PArhzJBv7';

export default {
    createOrUpdate({
                             commit
                         }, payload) {
        return new Promise((resolve, reject) => {
          let method = payload.id ? axios.patch(`${API}/regions/${payload.id}`, payload) : axios.post(`${API}/regions`, payload)
            method
                .then(({
                           data
                       }) => {
                    resolve(data);
                })
                .catch(error => reject(error.response));
        });
    },
  fetch({
                   commit
                 }, id) {
    return new Promise((resolve, reject) => {
      axios.get(`${API}/regions/${id}`)
        .then(({
                 data: {
                   data
                 }
               }) => {
          commit('SET_REGION')
          resolve(data);
        })
        .catch(error => reject(error.response));
    });
  },
  delete({
          commit
        }, id) {
    return new Promise((resolve, reject) => {
      axios.delete(`${API}/regions/${id}`)
        .then(({
                 data
               }) => {
          resolve(data);
        })
        .catch(error => reject(error.response));
    });
  },

}
