<template>
  <form ref="password_form" @submit.prevent="submit">

    <md-card>

      <md-card-header class="md-card-header-icon">
        <div class="card-icon">
          <md-icon>perm_identity</md-icon>
        </div>
        <h4 class="title">
          {{this.$route.params.id ? 'Edit' : 'Create'}} Region
        </h4>
      </md-card-header>

      <md-card-content v-if="loaded">
        <div class="md-layout">
          
<div class="md-layout-item md-size-100">
    <md-field>
      <label>Name </label>

      <md-textarea max="150" name="name" v-validate="'max:150'" v-model="body.name" :class="'textarea'" type="textarea"/>
    </md-field>
    <small
      class="form-text text-danger"
      v-show="errors.has('name')"
    >{{errors.first('name')}}</small>

  </div>

<div class="md-layout-item md-size-100">
    <md-field>
      <label>CountryId </label>

      <md-input  name="country_id" v-validate="'numeric'" v-model="body.country_id" :class="'number'" type="number"/>
    </md-field>
    <small
      class="form-text text-danger"
      v-show="errors.has('country_id')"
    >{{errors.first('country_id')}}</small>

  </div>

<div class="md-layout-item md-size-100">
    
      <label>Status </label>

      <md-checkbox  name="status" v-validate="'required'" v-model="body.status" :class="'checkbox'" type="checkbox"/>
    <hr>
    <small
      class="form-text text-danger"
      v-show="errors.has('status')"
    >{{errors.first('status')}}</small>

  </div>

<div class="md-layout-item md-size-100">
    <md-field>
      <label>CountryCode </label>

      <md-input max="5" name="country_code" v-validate="'max:5'" v-model="body.country_code" :class="'text'" type="text"/>
    </md-field>
    <small
      class="form-text text-danger"
      v-show="errors.has('country_code')"
    >{{errors.first('country_code')}}</small>

  </div>

<div class="md-layout-item md-size-100">
    <md-field>
      <label>StateCode </label>

      <md-input max="10" name="state_code" v-validate="'max:10'" v-model="body.state_code" :class="'text'" type="text"/>
    </md-field>
    <small
      class="form-text text-danger"
      v-show="errors.has('state_code')"
    >{{errors.first('state_code')}}</small>

  </div>

        </div>
      </md-card-content>

      <md-card-actions>
        <md-button type="submit" :disabled="loading || errors.length">
          {{this.$route.params.id ? 'Update' : 'Create'}}
        </md-button>
      </md-card-actions>
    </md-card>

  </form>
</template>

<script>
import {handleError, handleSuccess} from "@/helpers/response";
export default {
  title: `Region Form`,
  name: "Form",
  data: () => ({
    body: {

    },
    loading:false,
    loaded:false,
  }),

  methods: {
    submit() {
      this.$validator.validateAll().then(valid => {
        if (valid) {
          this.loading = true;
          this.sendRequest().then(() => {
            this.loading = false;
          });
        }
      });
    },
    async sendRequest() {
      this.$store
        .dispatch("region/createOrUpdate", this.body)
        .then(data => {
          handleSuccess(this, data.message);
        })
        .catch((response) => {
          handleError(this, response, 'Failed to perform action');
        });
    },
    fetchAndSetUser() {
      this.$store.dispatch('region/fetch', this.$route.params.id).then((data) => {
        this.body = data;
        this.loaded = true;
      })
    }
  },
  created() {

    if(this.$route.params.id) {
      this.fetchAndSetUser();
    } else {
      this.loaded = true;
    }
  },
  watch: {
    '$route' (to, from) {
      // react to route changes...
      if(to.params.id) {
        this.fetchAndSetUser();
      } else {
        this.body = {};
      }
    }
  }
};
</script>
